import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { DetailMenuComponent } from './detail-menu.component';
import { RouterModule, Routes } from '@angular/router';
import { NgZorroAntModule } from 'src/app/cafe/desktop/ng-zorro-ant.module';
import { IonicModule } from '@ionic/angular';
import { TranslateModule } from '@ngx-translate/core';
import { FormsModule } from '@angular/forms';

const routes: Routes = [
  {
    path: '',
    component: DetailMenuComponent
  },
  {
    path: 'detail',
    loadChildren: () => import('./detail-dishes/detail-dishes.module').then(m => m.DetailDishesModule)
  }
]

@NgModule({
  declarations: [DetailMenuComponent],
  imports: [
    CommonModule,
    RouterModule.forChild(routes),
    NgZorroAntModule,
    IonicModule,
    TranslateModule,
    FormsModule
  ]
})
export class DetailMenuModule { }
